
import Vue from "vue";

export default Vue.extend({
  components: {},
  name: "GADrawingCanvas",

  data() {
    return {
      currentColor: "#000000",
      firstClick: true,
      colorPickerDialogIsOpen: false,
      size: 10,
    };
  },

  methods: {
    handleFirstClick() {
      if (this.firstClick) {
        this.firstClick = !this.firstClick;
      }
    },
    handleColorPickerInput(newColor: string) {
      this.currentColor = newColor;
    },
    handleSizeChange(newSize: number) {
      this.size = newSize;
    },
    toDataURL() {
      // @ts-ignore
      const dataURL = this.$refs.signaturePad.value.toDataURL();
    },
    undo() {
      // @ts-ignore
      this.$refs.signaturePad!.undoSignature();
    },
    clear() {
      // @ts-ignore
      this.$refs.signaturePad!.clearSignature();
    },
    save() {
      // @ts-ignore
      const { isEmpty, data } = this.$refs.signaturePad!.saveSignature();
      this.$emit("drawing-saved", isEmpty, data);
      // @ts-ignore
      this.$refs.signaturePad!.clearSignature();
    },
  },

  computed: {
    options() {
      return {
        penColor: this.currentColor,
        maxWidth: this.size,
      };
    },
  },
});
